import request from '@/utils/request'
// 角色列表
export function findByCondition(data) {
  return request({
    url: `/sys/sysRole/findByCondition`,
    method: 'post',
    data
  })
}
// 添加角色
export function add(data) {
  return request({
    url: `/sys/sysRole/add`,
    method: 'post',
    data
  })
}
// 修改角色
export function update(data) {
  return request({
    url: `/sys/sysRole/update`,
    method: 'post',
    data
  })
}
// 查询角色详情
export function findById(data) {
  return request({
    url: `/sys/sysRole/findById`,
    method: 'post',
    data
  })
}
// 启用、禁用角色
export function updateEnable(data) {
  return request({
    url: `/sys/sysRole/updateEnable`,
    method: 'post',
    data
  })
}
// 目录树（总）
export function findAll() {
  return request({
    url: `/sys/sysMenu/findAll`,
    method: 'post',
  })
}