<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="角色名称：">
            <el-input style="width: 203px" v-model="searchList.roleName" clearable placeholder="角色名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="状态：">
            <el-select v-model="searchList.enabled" clearable placeholder="请选择状态">
              <el-option label="启用" value="0"></el-option>
              <el-option label="禁用" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/systemSetting/roleManage/newRole'})">新增角色</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="roleName" label="角色名称"></el-table-column>
          <el-table-column show-overflow-tooltip label="角色描述">
            <template slot-scope="scope">{{scope.row.roleDescription ? scope.row.roleDescription : '-'}}</template>
          </el-table-column>
          <el-table-column label="状态">
            <template slot-scope="scope">
              <div v-if="scope.row.enabled == 0">启用</div>
              <div class="dangerFont" v-if="scope.row.enabled == 1">禁用</div>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="创建时间"></el-table-column>
          <el-table-column label="操作" >
            <template slot-scope="scope">
              <el-button @click="$router.push({path:'/systemSetting/roleManage/roleInfo',query:{id:scope.row.id}})" type="text" size="small">查看</el-button>
              <el-button @click="$router.push({path:'/systemSetting/roleManage/editRole',query:{id:scope.row.id}})" type="text" size="small">修改</el-button>
              <el-button @click="changeItem(scope.row.id,0)" v-if="scope.row.enabled == 1" type="text" size="small">启用</el-button>
              <el-button class="dangerFont" @click="changeItem(scope.row.id,1)" v-if="scope.row.enabled == 0" type="text" size="small">禁用</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findByCondition ,updateEnable } from '@/api/systemSetting/roleManage'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      list:[],
      listTotal:0,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId;
    this.getData()
  },
  methods:{
    // 列表 - 获取数据
    getData(){
      console.log('请求的数据',this.searchList)
      findByCondition(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.roleName = ''
      this.searchList.enabled = ''
      this.getData()
    },
    // 启用 / 停用
    changeItem(e,i){
      this.$confirm(`确定${i == 0 ? '启':'禁'}用此角色？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        updateEnable({id:e,enabled:i}).then(res=>{
          console.log('启用、禁用角色',res)
          if(res.code == 200){
            this.$message({
              message: res.message,
              type: 'success'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
</style>